import React from "react";
import {
  Datagrid,
  List,
  TextField,
  FunctionField,
  DateField,
} from "react-admin";

export const GuitarList: React.FC = (props) => {
  return (
    <List
      {...props}
      title='Список гитар'
      sort={{ field: "dateCreated", order: "DESC" }}
    >
      <Datagrid rowClick='show' isRowSelectable={() => false}>
        <TextField label='Название' source='name' />
        <DateField label='Дата создания' source='dateCreated' />
        <FunctionField
          render={(data: any) => <img width={300} src={data.img} />}
        />
      </Datagrid>
    </List>
  );
};
