import React from "react";
import {
  SimpleShowLayout,
  Show,
  TextField,
  FunctionField,
  DateField,
} from "react-admin";

export const UserShow: React.FC = (props) => {
  return (
    <Show {...props}>
      <SimpleShowLayout>
        <TextField label='Логин' source='userName' />
        <DateField label='Дата регистрации' source='registrationDate' />
      </SimpleShowLayout>
    </Show>
  );
};
