import React from "react";
import { Datagrid, List, TextField, FunctionField } from "react-admin";

export const GuitarTypeList: React.FC = (props) => {
  return (
    <List {...props} title='Список типов гитар'>
      <Datagrid rowClick='edit' isRowSelectable={() => false}>
        <TextField label='Название' source='name' />
        <FunctionField render={(data: any) => <img src={data.img} />} />
      </Datagrid>
    </List>
  );
};
