import React, { FC } from "react";
import {
  Edit,
  TextInput,
  SimpleForm,
  required,
  ImageInput,
  ImageField,
  ReferenceInput,
  AutocompleteInput,
  minValue,
  maxValue,
} from "react-admin";

export const GuitarEdit: React.FC = (props) => {
  return (
    <Edit {...props}>
      <SimpleForm>
        <TextInput label='Название' source='name' validate={required()} />
        <TextInput
          label='Описание'
          source='description'
          validate={required()}
        />
        <TextInput
          label='Цена'
          source='cost'
          validate={[required(), minValue(1), maxValue(3000000)]}
        />
        <TextInput
          label='Количество'
          source='count'
          validate={[required(), minValue(1), maxValue(100)]}
        />
        <TextInput
          label='Рейтинг'
          source='rating'
          validate={[required(), minValue(0), maxValue(10)]}
        />
        <ReferenceInput
          label='Тип гитары'
          source='guitarTypeId'
          reference='guitartypes'
          validate={required()}
        >
          <AutocompleteInput optionText='name' />
        </ReferenceInput>
        <ImageInput source='image' label='Картинка' multiple={false}>
          <PreviewImage source='src' />
        </ImageInput>
        <ImageInput
          source='files'
          label='Детальные картинки'
          validate={required()}
          multiple
        >
          <PreviewImage source='src' />
        </ImageInput>
      </SimpleForm>
    </Edit>
  );
};

const PreviewImage: FC<any> = ({ record, source }) => {
  if (typeof record == "string") {
    record = {
      [source]: record,
    };
  }
  return <ImageField record={record} source={source} />;
};
