import * as React from "react";
import { useState } from "react";
import { useSelector } from "react-redux";
import { useMediaQuery, Box } from "@material-ui/core";
import MapIcon from "@material-ui/icons/Map";
import { MenuItemLink } from "react-admin";

const Menu: React.FC<any> = ({ onMenuClick, logout, dense = false }) => {
  const [state, setState] = useState<{ [p: string]: boolean }>({
    diseas: true,
  });
  const isXSmall = useMediaQuery((theme: any) => theme.breakpoints.down("xs"));
  const open = useSelector((state: any) => state.admin.ui.sidebarOpen);
  useSelector((state: any) => state.theme); // force rerender on theme change

  const handleToggle = (menu: string) => {
    setState((state) => ({ ...state, [menu]: !state[menu] }));
  };

  const item = localStorage.getItem("permissions");
  const permissions: string[] | undefined = item && JSON.parse(item);

  const admin = permissions?.includes("Admin");

  return (
    <Box mt={1}>
      <MenuItemLink
        to={`/users`}
        primaryText={"Пользователи"}
        onClick={onMenuClick}
        sidebarIsOpen={open}
        dense={dense}
        leftIcon={<MapIcon />}
      />
      <MenuItemLink
        to={`/guitars`}
        primaryText={"Гитары"}
        onClick={onMenuClick}
        sidebarIsOpen={open}
        dense={dense}
        leftIcon={<MapIcon />}
      />
      <MenuItemLink
        to={`/guitartypes`}
        primaryText={"Типы гитар"}
        onClick={onMenuClick}
        sidebarIsOpen={open}
        dense={dense}
        leftIcon={<MapIcon />}
      />
      <MenuItemLink
        to={`/orders`}
        primaryText={"Заказы"}
        onClick={onMenuClick}
        sidebarIsOpen={open}
        dense={dense}
        leftIcon={<MapIcon />}
      />
      {isXSmall && logout}
    </Box>
  );
};

export default Menu;
