import React from "react";
import {
  SimpleShowLayout,
  Show,
  TextField,
  FunctionField,
  ReferenceField,
  DateField,
  List,
  Datagrid,
} from "react-admin";

export const OrderList: React.FC = (props) => {
  return (
    <List {...props} sort={{ field: "orderDate", order: "DESC" }}>
      <Datagrid>
        <ReferenceField label='Пользователь' source='userId' reference='users'>
          <TextField source='userName' />
        </ReferenceField>
        <DateField label='Дата покупки' source='orderDate' />
        <FunctionField
          label='Цена'
          render={(data: any) =>
            data.cartItems.reduce(
              (accumulator: number, item: any) =>
                accumulator + item.count * item.guitar.cost
            ) + " руб."
          }
        />
      </Datagrid>
    </List>
  );
};
