import React from "react";
import { Admin, Resource, Login } from "react-admin";
import PostIcon from "@material-ui/icons/Book";
import polyglotI18nProvider from "ra-i18n-polyglot";

import { dataProvider } from "./configuration";

import authProvider from "./authProvider";
import LoginForm from "./pages/LoginForm";

import "./App.css";
import { theme } from "./static/theme";

import { UserList, UserShow } from "./entities/users";
import {
  GuitarList,
  GuitarShow,
  GuitarCreate,
  GuitarEdit,
} from "./entities/guitars";
import {
  GuitarTypeEdit,
  GuitarTypeList,
  GuitarTypeCreate,
} from "./entities/guitarType";
import { OrderList, OrderShow } from "./entities/orders";

import { Menu } from "./Layout";

const russianMessages = require("ra-language-russian");

const i18nProvider = polyglotI18nProvider(() => russianMessages, "ru");

const LoginPage = (props: any) => {
  return (
    <Login {...props}>
      <React.Fragment>
        <LoginForm {...props} />
      </React.Fragment>
    </Login>
  );
};

export default function App() {
  return (
    <Admin
      theme={theme}
      dataProvider={dataProvider}
      i18nProvider={i18nProvider}
      authProvider={authProvider}
      loginPage={LoginPage}
      menu={Menu}
    >
      <Resource
        name='users'
        list={UserList}
        show={UserShow}
        icon={PostIcon}
        options={{
          label: "Пользователи",
        }}
      />
      <Resource
        name='guitars'
        list={GuitarList}
        show={GuitarShow}
        create={GuitarCreate}
        edit={GuitarEdit}
        icon={PostIcon}
        options={{
          label: "Гитары",
        }}
      />
      <Resource
        name='guitartypes'
        list={GuitarTypeList}
        edit={GuitarTypeEdit}
        create={GuitarTypeCreate}
        icon={PostIcon}
        options={{
          label: "Типы гитар",
        }}
      />
      <Resource
        name='orders'
        list={OrderList}
        show={OrderShow}
        icon={PostIcon}
        options={{
          label: "Заказы",
        }}
      />
    </Admin>
  );
}
