import React from "react";
import {
  SimpleShowLayout,
  Show,
  TextField,
  FunctionField,
  DateField,
  ReferenceField,
} from "react-admin";

export const GuitarShow: React.FC = (props) => {
  return (
    <Show {...props}>
      <SimpleShowLayout>
        <TextField label='Название' source='name' />
        <TextField label='Описание' source='description' />
        <TextField label='Цена' source='cost' />
        <TextField label='Количество' source='count' />
        <TextField label='Рейтинг' source='rating' />
        <ReferenceField
          label='Тип гитары'
          source='guitarTypeId'
          reference='guitartypes'
        >
          <TextField source='name' />
        </ReferenceField>
        <DateField label='Дата регистрации' source='dateCreated' />
        <FunctionField
          label='Фото'
          render={(data: any) => <img width={300} src={data.img} />}
        />
        <FunctionField
          label='Детальные фото'
          render={(data: any) => (
            <>
              {data.detailedImages.map((x: any) => (
                <img width={300} src={x} />
              ))}
            </>
          )}
        />
      </SimpleShowLayout>
    </Show>
  );
};
