import React from "react";
import { Link } from "react-router-dom";
import {
  SimpleShowLayout,
  Show,
  TextField,
  FunctionField,
  ReferenceField,
  ArrayField,
  Datagrid,
  DateField,
} from "react-admin";

export const OrderShow: React.FC = (props) => {
  return (
    <Show {...props}>
      <SimpleShowLayout>
        <ReferenceField label='Пользователь' source='userId' reference='users'>
          <TextField source='userName' />
        </ReferenceField>
        <DateField label='Дата покупки' source='orderDate' />
        <FunctionField
          label='Цена'
          render={(data: any) =>
            data.cartItems.reduce(
              (accumulator: number, item: any) =>
                accumulator + item.count * item.guitar.cost
            ) + " руб."
          }
        />
        <ArrayField source='cartItems' label='Корзина'>
          <Datagrid>
            <TextField source='guitarCount' label='Количество гитар' />
            <FunctionField
              label='Название'
              render={(data: any) => data.guitar.name}
            />
            <FunctionField
              label='Цена'
              render={(data: any) => data.guitar.cost + " руб."}
            />

            <FunctionField
              label='Просмотр'
              render={(data: any) => (
                <Link to={"/guitars/" + data.guitar.id + "/show"}>
                  Посмотреть
                </Link>
              )}
            />
          </Datagrid>
        </ArrayField>
      </SimpleShowLayout>
    </Show>
  );
};
