// Core
import jsonServerProvider from "ra-data-json-server";

// Components
import config from "../../config";
import { AdminHttpClient } from "./AdminHttpClient";

const myDataProvider = jsonServerProvider(config.baseApiUrl, AdminHttpClient);
export const dataProvider = {
  ...myDataProvider,
  create: (resource: any, params: any) => {
    console.log(resource);

    if (resource !== "guitars") {
      return myDataProvider.create(resource, params);
    }

    let formData = new FormData();

    formData.append("name", params.data.name);
    formData.append("description", params.data.description);
    formData.append("rating", params.data.rating);
    formData.append("count", params.data.count);
    formData.append("cost", params.data.cost);
    formData.append("guitarTypeId", params.data.guitarTypeId);
    formData.append("files", params.data.image.rawFile);
    params.data.files.forEach((x: any) => formData.append("files", x.rawFile));

    return AdminHttpClient(`${config.baseApiUrl}/${resource}`, {
      method: "POST",
      body: formData,
    }).then(({ json }: any) => ({
      data: { ...params.data, id: json.id },
    }));
  },
  update: (resource: any, params: any) => {
    if (resource !== "guitars") {
      return myDataProvider.update(resource, params);
    }

    let formData = new FormData();

    formData.append("name", params.data.name);
    formData.append("description", params.data.description);
    formData.append("rating", params.data.rating);
    formData.append("count", params.data.count);
    formData.append("cost", params.data.cost);
    formData.append("guitarTypeId", params.data.guitarTypeId);
    formData.append("files", params.data.image?.rawFile);
    params.data.files?.forEach((x: any) => formData.append("files", x.rawFile));

    return AdminHttpClient(`${config.baseApiUrl}/${resource}/${params.id}`, {
      method: "PUT",
      body: formData,
    }).then(({ json }: any) => ({
      data: { ...params.data, id: json.id },
    }));
  },
};
